import React, { Component } from 'react';
import { Form } from 'react-bootstrap';

export class Calculator extends Component {

  render () {
    return (
      <div>
        <div className='card-body'>
          <h2 className="mb-1 mb-sm-0">EverBNB Earnings Manager</h2>
        </div>
        
        <div className="row">
          <div className="col-12 grid-margin stretch-card">
            <div className="card corona-gradient-card">
              <div className="card-body py-0 px-0 px-sm-1">
                <div className="row align-items-center">
                  <div className="card-body col-5 col-sm-4 col-xl-1">
                    <h2><i className="mdi mdi-book-open-variant"></i></h2>
                  </div>
                  <div className=" col-sm-1 col-xl-8 p-0">
                    <h3 className="mb-1 mb-sm-0">Please enter your address above</h3>
                  </div>
                  <div className="col-3 col-sm-2 col-xl-2 pl-0 text-center">
                    <button className="btn btn-outline-light btn-rounded get-started-btn">Buy EVERBNB</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-xl-12 col-sm-6 grid-margin stretch-card">
            <div className="card">
              <div className="card-body">
                <div className="row align-items-center align-self-start">
                  <div className="grid-margin text-center">
                    <h3 className="mb-1 mb-sm-0">Estimations are based on a default of the last 24h of trading volume Change 
                        the volume to predict earnings based on other volume figures Trading Volume = $</h3>
                  </div>

                  <form className="forms-sample col-xl-12">
                    <Form.Group className="row">
                      <label htmlFor="exampleInputUsername2" className="col-xl-4 col-form-label"></label>
                      <div className="col-xl-4">
                        <Form.Control type="text" className="form-control" id="exampleInputUsername2" placeholder="$" />
                      </div>
                    </Form.Group>
                  </form> 
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-xl-12 col-sm-6 grid-margin stretch-card">
            <div className="card">
              <div className="card-body">
                <div className="row align-items-center align-self-start col-xl-12">
                  <div className="grid-margin col-xl-12 text-center">
                    <h3 className="mb-1 mb-sm-0">Change the token amount to predict earnings based on other token amount Token Amount =</h3>
                  </div>
                  <form className="forms-sample col-xl-12">
                    <Form.Group className="row">
                      <label htmlFor="exampleInputUsername2" className="col-xl-4 col-form-label"></label>
                      <div className="col-xl-4">
                        <Form.Control type="text" className="form-control" id="exampleInputUsername2" placeholder="$" />
                      </div>
                    </Form.Group>
                  </form> 
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="row text-center">
          <div className="col-xl-6 col-sm-6 grid-margin stretch-card">
            <div className="card">
              <div className="card-body">
                <div className="item grid-margin">
                  <img src={require('../../assets/images/bitcoins/1.jpg')} alt="face" className="rounded-circle" />
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start grid-margin text-center col-xl-12">
                    <h2 className="mb-0 text-center col-xl-12">Your 0 EverBNB Earns:</h2>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0.00 BNB ($0.00) Per Day</h3>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0.00 BNB ($0.00) Per Week</h3>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0.00 BNB ($0.00) Per Mondth</h3>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0.00 BNB ($0.00) Per Year</h3>
                  </div>
                </div>
                <div className="col-12 grid-margin">
                  <div className="d-flex align-items-center align-self-start grid-margin">
                    <h4 className="mb-0 text-center col-xl-12">Estimations are based on $22,523 trading volume</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-6 col-sm-6 grid-margin stretch-card">
            <div className="card">
              <div className="card-body">
                <div className="item grid-margin">
                  <img src={require('../../assets/images/bitcoins/1.jpg')} alt="face" className="rounded-circle" />
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start grid-margin">
                    <h2 className="mb-0">By Reinvesting Reward Every Day, Your 0 EVERBNB Becomes:</h2>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0 EVERBNB (0x Earnings) In a Week</h3>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0 EVERBNB (0x Earnings) In a Month</h3>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0 EVERBNB (0x Earnings) In a 6Months</h3>
                  </div>
                </div>
                <div className="col-12">
                  <div className="d-flex align-items-center align-self-start">
                    <h3 className="mb-0 text-center col-xl-12">0 EVERBNB (0x Earnings) In a Year</h3>
                  </div>
                </div>
                <div className="col-12 grid-margin">
                  <div className="d-flex align-items-center align-self-start">
                    <h4 className="mb-0 text-center col-xl-12 grid-margin">Estimations are based on current $EverBNB price ($0.0009677500)</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div> 
    );
  }
}

export default Calculator;